<template>
     <section>
                                     <div class="border p-3"  >
                                <dynamic-form v-if="DataLoaded" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                                  
                                </dynamic-form>
                             </div>
        
                                    <!-- 
